$background_color: #28292e;
$color_main: #4fc4f9;
$color_text: #fff;
$color_hint: #ccc;
body {
  width: 100%;
  height: 100vh;
  display: flex;
  #root {
    flex-grow: 1;
    width: 100%;
    display: flex;
    .App {
      flex-grow: 1;
      position: relative;
      display: flex;
      flex-direction: column;
      align-items: center;
      background-color: $background_color;
      .margin-top {
        height: 5%;
      }
      .label {
        width: 30%;
        color: $color_hint;
        margin:0 0 15px 0;
        span {
          color: $color_main;
          font-size: 2rem;
        }
      }
      h3 {
        color: $color_text;
        margin-top: 0;
      }
      a {
        color: $color_text;
        &:hover {
          color: $color_main;
        }
      }
      .bVxZhR {
        background-color: transparent;
        height: auto;
      }
      .heQTiL {
        background-color: $background_color;
        min-height: 100px;
        height: auto;
        .dnVegZ {
          background-color: $color_main !important;
          color: $color_text !important;
        }
        .hCBOlN {
          position: relative;
        }
      }
      .hidden {
        display: none;
      }
      .disconnect-view {
        width: 30%;
        color: $color_main;
      }
      .mask {
        position: absolute;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        width: 100%;
        height: 100%;
        display: flex;
        background: $background_color;
        top: 0;
        left: 0;
        opacity: 0.8;
        .result {
          width: 30%;
          color: #00ff00;
        }
      }
      .full-height {
        height: 100%;
      }
      .not-allowed {
        cursor: not-allowed;
      }
      @media (max-width: 1000px) {
        .disconnect-view {
          width: 70%;
        }
        .label {
          width: 70%;
          span {
            color: $color_main;
            font-size: 2rem;
          }
        }
      }
    }
  }
  .jKEXsb {
    background-color: $color_main !important;
    color: $color_text !important;
  }
  .hSoFMV {
    position: relative;
  }
}
